.welcomewindow {
    width: 100%;
    height: 100%;
    padding: 0.5em;
    margin: 0;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    box-sizing: border-box;
}

.welcomewindow-info {
    width: 100%;
    height: 70%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 2.5em;
}

.welcomewindow-input {
    width: 100%;
    height: 10%;
    display: flex;
    justify-content: center;
    background-color: ghostwhite;
    border-radius: 2em;


}

.welcomewindow-inputbar {
    width: 90%;
    height: 100%;

}

.welcomewindow-inputbutton {
    width: 10%;
    height: 100%;
    background-color: #024059;
    color: ghostwhite;
    padding-right: 0.001em;
    border-radius: 2em;

}